import React, { useState, useEffect } from 'react'
import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import Snackbar from '@mui/material/Snackbar';
import InstitutionReg from '../registration/institutionReg/InstitutionReg';
import UserReg from '../registration/userReg/UserReg';
import Subscription from '../registration/subscription/Subscription'
import { Step, StepLabel, Stepper } from '@material-ui/core';
import axios from 'axios';
import RegSummary from './regSummary/RegSummary';
import Modal from '@mui/material/Modal';
import Loading from '../components/loading/Loading';
import { Alert } from '@mui/material';

const Registration = () => {

    const [activeStep, setActiveStep] = useState(0);
    const [institutionDetails, setInstitutionDetails] = useState({});
    const [userDetails, setUserDetails] = useState({});
    const [selectedPlan, setSelectedPlan] = useState({});
    const [isPageLoading, setisPageLoading] = useState(false);
    const [isPageLoadingFailed, setisPageLoadingFailed] = useState(false);
    const [errorMsg, setErrorMsg] = useState({ open: false, message: '', vertical: 'top', horizontal: 'center', severity:'success' });

    const { vertical, horizontal, open, message, severity } = errorMsg;

    useEffect(() => {
        handleRegestration();
    }, [selectedPlan]);

    const handleSnackbarClose = () => {
        setErrorMsg({ open: false, message: '', vertical: 'top', horizontal: 'center', });
    }
    const handleUpdateActiveStep = (nextStage) => {
        setActiveStep(nextStage)
    }

    const handleScbscriptionActions = (step, selectedplan) => {
        if (step === 1) {
            handleUpdateActiveStep(step);
        }
        else {
            setSelectedPlan(selectedplan);
        }

    }

    const handleRegestration = async () => {
        console.log('selectedPlan', selectedPlan);
        if (JSON.stringify(selectedPlan) !== JSON.stringify({})) {
            try {
                setisPageLoading(true);
                const { data } = await axios.post(`${process.env.REACT_APP_SERVER}/signup`, {
                    institutionData: institutionDetails,
                    userData: userDetails,
                    subscriptionData: selectedPlan
                });
                console.log(data);
                if (data) {
                    setErrorMsg({ open: true, message: `Email has been sent to ${userDetails.email} to set the password.`, vertical: 'top', horizontal: 'center', severity:'success' });
                    window.open(data.url, "_self");
                }
                setisPageLoading(false);
            }
            catch (error) {
                console.log(error);
                if (error.response?.status === 400 || error.response?.status === 502) {
                    setErrorMsg({ open: true, message: error.response?.data?.error, vertical: 'top', horizontal: 'center',severity:'error' });
                }
                setisPageLoading(false);
                //setisPageLoadingFailed(true);
            }
        }
    }

    if (isPageLoadingFailed) {
        return (
            <Box style={{ paddingTop: '50%', backgroundColor: 'white' }}>
                <Loading isPageLoading={isPageLoading} />
            </Box>
        )
    }
    else if (isPageLoading) {
        return (
            <Box style={{ top: '50%' }}>
                <Loading isPageLoading={isPageLoading} />
            </Box>
        )
    }
    else {
        return (
            <React.Fragment>
                <div>
                    <Stepper alternativeLabel activeStep={activeStep}>
                        <Step>
                            <StepLabel>Instuition Registration</StepLabel>
                        </Step>
                        <Step>
                            <StepLabel>User Registration</StepLabel>
                        </Step>
                        <Step>
                            <StepLabel>Subscription</StepLabel>
                        </Step>
                    </Stepper>
                    {
                        activeStep === 0 ?
                            <InstitutionReg institutionDetails={institutionDetails} updateActiveStep={handleUpdateActiveStep} handleInstitutionDetails={(data) => { setInstitutionDetails(data) }} />
                            : activeStep === 1 ?
                                <UserReg userDetails={userDetails} updateActiveStep={handleUpdateActiveStep} handleUserDetails={(userData) => { setUserDetails(userData) }} />
                                : activeStep === 2 ?
                                    <Subscription onActions={handleScbscriptionActions} currentProduct = ''/> : ''
                    }
                </div>
                <Stack spacing={2} sx={{ width: '100%' }}>
                    <Snackbar open={open} autoHideDuration={6000} onClose={handleSnackbarClose} anchorOrigin={{ vertical, horizontal }} key={vertical + horizontal}>
                        <Alert onClose={handleSnackbarClose} severity={severity} sx={{ width: '100%' }}>
                            {message}
                        </Alert>
                    </Snackbar>
                </Stack>
            </React.Fragment>
        )
    }
}

export default Registration